import React, { useEffect, useState } from "react";
import Nav from "react-bootstrap/Nav";
import css from "./Navbar.module.css";
import NavDropdown from "react-bootstrap/NavDropdown";

function Navbar() {
  const [winWidth, setWinWidth] = useState(0);
  useEffect(() => setWinWidth(window.innerWidth), [])
  window.onresize = () => {
    setWinWidth(window.innerWidth);
  };

  return (
    <Nav
      activeKey="/home"
      onSelect={(selectedKey) => alert(`selected ${selectedKey}`)}
      className={`justify-content-end p-2 font-monospace ${css.nav}`}
      title={
        <div className={css.lines}>
          <div></div>
          <div></div>
          <div></div>
        </div>
      }
      as={winWidth < 800 ? NavDropdown : "li"}
    >
      <Nav.Item>
        <Nav.Link eventKey="/">HOME</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link eventKey="/licensing">
          KNIVES: AVAILABLE FOR LICENSING
        </Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link eventKey="/serial">SERIAL KNIVES</Nav.Link>
      </Nav.Item>
      <Nav.Item>
        <Nav.Link eventKey="/contacts">CONTACT US</Nav.Link>
      </Nav.Item>
    </Nav>
  );
}

export default Navbar;
